<template>

<div>
<!--  -->
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>
<!--  -->
  <el-card>
    <!--  -->
    <el-row :gutter="20">
      <el-col :span="7">
        <el-input placeholder="" v-model="queryInfo.query" clearable @clear="getUserList">
          <el-button slot="append" icon="el-icon-search" @click="getUserList"></el-button>
        </el-input>
      </el-col>
      <el-col :span="4">
        <el-button type="primary" @click="addDialogVisible = true"></el-button>
      </el-col>
    </el-row>
<!---->
    <el-table :data="userList" style="width: 100%" stripe border>
      <el-table-column label="#" type="index"></el-table-column>
      <el-table-column label="" prop="username"></el-table-column>
      <el-table-column label="" prop="email"></el-table-column>
      <el-table-column label="" prop="mobile"></el-table-column>
      <el-table-column label="" prop="role_name"></el-table-column>
      <el-table-column label="" prop="mg_state">
        <template v-slot="scope">
          <el-switch v-model="scope.row.mg_state" active-color="#13ce66" inactive-color="#ff4949"
          @change="userStateChanged(scope.row)">
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label="" width="180px">
        <template v-slot="scope">
          <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
            <el-button type="primary" icon="el-icon-edit" size="mini"
                       @click="showEditDialog(scope.row.id)"></el-button>
          </el-tooltip>
          <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
            <el-button type="danger" icon="el-icon-delete" size="mini"
                       @click="removeUserById(scope.row.id)"></el-button>
          </el-tooltip>
          <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
            <el-button type="warning" icon="el-icon-setting" size="mini"
                       @click="setRole(scope.row)"></el-button>
          </el-tooltip>
        </template>
      </el-table-column>
    </el-table>
<!--    -->
    <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="queryInfo.pagenum"
        :page-sizes="[1, 2, 5, 10]"
        :page-size="queryInfo.pagesize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="total">
    </el-pagination>
  </el-card>

<!---->
  <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addDialogClosed">
<!--    -->
    <el-form :model="addForm" :rules="addFormRules" ref="addFormRef" label-width="70px">
      <el-form-item label="" prop="username">
        <el-input v-model="addForm.username"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password">
        <el-input v-model="addForm.password"></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="addForm.email"></el-input>
      </el-form-item>
      <el-form-item label="" prop="mobile">
        <el-input v-model="addForm.mobile"></el-input>
      </el-form-item>
    </el-form>
<!--    -->
    <span slot="footer" class="dialog-footer">
    <el-button @click="addDialogVisible = false"> </el-button>
    <el-button type="primary" @click="addUser"> </el-button>
  </span>
  </el-dialog>

  <!---->
  <el-dialog title="" :visible.sync="editDialogVisible" width="50%"  @close="editDialogClosed">
    <!--    -->
    <el-form :model="editForm" :rules="editFormRules" ref="editFormRef" label-width="70px">
      <el-form-item label="">
        <el-input v-model="editForm.username" disabled></el-input>
      </el-form-item>
      <el-form-item label="" prop="email">
        <el-input v-model="editForm.email"></el-input>
      </el-form-item>
      <el-form-item label="" prop="mobile">
        <el-input v-model="editForm.mobile"></el-input>
      </el-form-item>
    </el-form>
    <!--    -->
    <span slot="footer" class="dialog-footer">
    <el-button @click="editDialogVisible = false"> </el-button>
    <el-button type="primary" @click="editUserInfo"> </el-button>
  </span>
  </el-dialog>

<!--  -->
  <el-dialog title="" :visible.sync="allotRoleDialogVisible" width="50%" @close="setRoleDialogClosed">
    <div>
      <p> : {{userInfo.username}}</p>
      <p> : {{userInfo.role_name}}</p>
      <p> :
        <el-select v-model="selectedRoleId" placeholder="" >
          <el-option v-for="item in rolesList" :key="item.id" :label="item.roleName" :value="item.id">
          </el-option>
        </el-select>
      </p>
    </div>
    <span slot="footer" class="dialog-footer">
    <el-button @click="allotRoleDialogVisible = false"> </el-button>
    <el-button type="primary" @click="saveRoleInfo"> </el-button>
  </span>
  </el-dialog>

</div>
</template>

<script>
export default {
  name: "User",
  data() {
    // 
    var checkEmai = (rule, value, cb) => {
      // 
      const regEmail = /^([a-zA-Z0-9_-])+@([a-zA-Z0-9_-])+(\.[a-zA-Z0-9_-])+/
      if (regEmail.test(value)) {
        // 
        return cb()
      } else {
        cb(new Error(''))
      }
    }
    // 
    var checkMobile = (rule, value, cb) => {
      // 
      const regMobile = /^1[34578]\d{9}$/
      if (regMobile.test(value)) {
        return cb()
      }
      //
      cb(new Error(''))
    }
    return {
      //  
      queryInfo: {
        query: '',
        // 
        pagenum: 1,
        pagesize: 10
      },
      userList: [],
      total: 0,
      // 
      addDialogVisible: false,
      editDialogVisible: false,
      allotRoleDialogVisible: false,
      // 
      addForm: {
        username: '',
        password: '',
        email: '',
        mobile: '',

      },
      // 
      addFormRules: {
        username: [
          {required: true, message: '', trigger: 'blur'},
          {min: 3, max: 10, message: '3~10', trigger: 'blur'}
        ],
        password: [
          {required: true, message: '', trigger: 'blur'},
          {min: 6, max: 15, message: '6~15', trigger: 'blur'}
        ],
        email: [
          {required: true, message: '', trigger: 'blur'},
          {validator: checkEmai, trigger: 'blur'}
        ],
        mobile: [
          {required: true, message: '', trigger: 'blur'},
          {validator: checkMobile, trigger: 'blur'}
        ]
      },
      editForm: [],
      editFormRules: {
        username: [
          {required: true, message: '', trigger: 'blur'},
          {min: 3, max: 10, message: '3~10', trigger: 'blur'}
        ],
        email: [
          {required: true, message: '', trigger: 'blur'},
          {validator: checkEmai, trigger: 'blur'}
        ],
        mobile: [
          {required: true, message: '', trigger: 'blur'},
          {validator: checkMobile, trigger: 'blur'}
        ]
      },
      userInfo: {},
      rolesList: [],
      selectedRoleId: ''
    }
  },
  created() {
    this.getUserList()
  },
  methods: {
    async getUserList() {
      const {data: res} = await this.$http.get('users', {
        params: this.queryInfo
      })
      if (res.meta.status !== 200) return this.$message.error("!")
      this.userList = res.data.users
      this.total = res.data.total
    },
    // pagesize
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getUserList()
    },
    // 
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum = newPage
      this.getUserList()
    },
    async userStateChanged(userInfo) {
      console.log(userInfo);
      const {data: res} = await this.$http.put(`users/${userInfo.id}/state/${userInfo.mg_state}`)
      if (res.meta.status !== 200) {
        userInfo.mg_state = !userInfo.mg_state
        return this.$message.error('!')
      } else {
        this.$message.success('')

      }
    },
    // 
    addDialogClosed() {
      this.$refs.addFormRef.resetFields()
    },
    editDialogClosed() {
      this.$refs.editFormRef.resetFields()
    },
    setRoleDialogClosed() {
      this.selectedRoleId = '',
          this.userInfo = {}
    },
    // 
    addUser() {
      this.$refs.addFormRef.validate(async valid => {
        if (!valid) return
        const {data: res} = await this.$http.post('users', this.addForm)
        if (res.meta.state !== 201) {
          this.$message.error('')
        }
        this.$message.success('')
        // 
        this.addDialogVisible = false
        // 
        this.getUserList()
      })
    },
    // 
    async showEditDialog(id) {
      const {data: res} = await this.$http.get('users/' + id)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.editForm = res.data
      this.editDialogVisible = true
    },
    editUserInfo() {
     this.$refs.editFormRef.validate( async valid=> {
       if (!valid) return Error
       const {data: res} = await this.$http.put('users/' + this.editForm.id, {
         email: this.editForm.email,
         mobile: this.editForm.mobile
       })
       if (res.meta.status !== 200) {
         return this.$message.error('')
       }
       // 
       this.editDialogVisible = false
       // 
       this.getUserList()
       // 
       this.$message.success('!')

     })
    },
    async removeUserById(id) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err=> err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
       const {data: res} = await this.$http.delete('users/' + id)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getUserList()
    },
    async setRole(userInfo) {
      console.log(userInfo);
      this.userInfo = userInfo
      const {data: res} = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error("")
      }
      this.rolesList = res.data
      console.log(this.rolesList);
      this.allotRoleDialogVisible = true
    },
   async saveRoleInfo() {
      if (!this.selectedRoleId) {
        return this.$message.error('')
      }
      const {data: res} = await this.$http.put(`users/${this.userInfo.id}/role`, {
        rid: this.selectedRoleId})
     if (res.meta.status !== 200) {
       return this.$message.error('')
     }
     this.$message.success('')
     this.getUserList()
     this.allotRoleDialogVisible = false
    }
  }
}
</script>

<style scoped>

</style>