<template>
<div>
  <el-breadcrumb separator="/">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>

  <el-card>
    <el-row>
      <el-col>
        <el-button type="primary" @click="addRolesVisible = true"></el-button>
      </el-col>
    </el-row>

    <el-table :data="rolesList" border stripe>
      <el-table-column type="expand">
        <template v-slot="scope">
          <el-row :class="['bdbottom',index1 === 0 ? 'bdtop' : '', 'vcenter']" v-for="(item1, index1) in scope.row.children" :key="item1.id">
            <!--            -->
            <el-col :span="5">
              <el-tag closable @close="removeRightsById(scope.row,item1.id)">{{item1.authName}}</el-tag>
              <i class="el-icon-caret-right"></i>
            </el-col>
            <!--            -->
            <el-col :span="19">
              <el-row :class="[index2 === 0 ? '' : 'bdtop','vcenter']" v-for="(item2, index2) in item1.children"  :key="item2.id">
                <el-col :span="6">
                  <el-tag type="success" closable @close="removeRightsById(scope.row,item2.id)">{{item2.authName}}</el-tag>
                  <i class="el-icon-caret-right"></i>
                </el-col>
                <el-col :span="18">
                  <el-tag type="warning" v-for="(item3,index3) in item2.children" closable
                  @close="removeRightsById(scope.row,item3.id)">{{item3.authName}}</el-tag>
                </el-col>
              </el-row>
            </el-col>

          </el-row>



<!--         <pre>{{scope.row}}</pre>-->
        </template>
      </el-table-column>
      <el-table-column label="#" type="index"></el-table-column>
      <el-table-column label="" prop="roleName"></el-table-column>
      <el-table-column label="" prop="roleDesc"></el-table-column>
      <el-table-column label="" width="300px">
        <template v-slot="scope">
          <el-button type="primary" icon="el-icon-edit" size="mini" @click="showEditVisible(scope.row.id)"></el-button>
          <el-button type="danger" icon="el-icon-delete" size="mini" @click="removeRolesById(scope.row.id)"></el-button>
          <el-button type="warning" icon="el-icon-setting" size="mini" @click="showSetRightDialogue(scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
  </el-card>
<!--  -->
  <el-dialog title="" :visible.sync="addRolesVisible" width="35%"  @close="addRolesClosed">
    <!--    -->
    <el-form :model="addRoles" :rules="editRolesRules" ref="addRolesRef" label-width="80px">
      <el-form-item label="" prop="roleName">
        <el-input v-model="addRoles.roleName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="roleDesc">
        <el-input v-model="addRoles.roleDesc"></el-input>
      </el-form-item>
    </el-form>
    <!--    -->
    <span slot="footer" class="dialog-footer">
    <el-button @click="addRolesVisible = false"> </el-button>
    <el-button type="primary" @click="addRolesInfo"> </el-button>
  </span>
  </el-dialog>
<!---->
  <el-dialog title="" :visible.sync="editRolesVisible" width="50%"  @close="editRolesClosed">
    <!--    -->
    <el-form :model="editRoles" :rules="editRolesRules" ref="editRolesRef" label-width="80px">
      <el-form-item label="" prop="roleName">
        <el-input v-model="editRoles.roleName"></el-input>
      </el-form-item>
      <el-form-item label="" prop="roleDesc">
        <el-input v-model="editRoles.roleDesc"></el-input>
      </el-form-item>
    </el-form>
    <!--    -->
    <span slot="footer" class="dialog-footer">
    <el-button @click="editRolesVisible = false"> </el-button>
    <el-button type="primary" @click="editRolesInfo"> </el-button>
  </span>
  </el-dialog>


<!--  -->

  <el-dialog title="" :visible.sync="setRightDialogVisible" width="50%" @close="setRightDialogClosed">
      <el-tree :data="rightsList" :props="treeProps"
               show-checkbox node-key="id" default-expand-all
               :default-checked-keys = 'defKeys' ref="treeRef"
      ></el-tree>


    <span slot="footer" class="dialog-footer">
    <el-button @click="setRightDialogVisible = false"> </el-button>
    <el-button type="primary" @click="allotRights"> </el-button>
  </span>
  </el-dialog>

</div>
</template>

<script>
export default {
  name: "Roles",
  data() {
    return {
      rolesList: [],
      rightsList: [],
      addRolesVisible: false,
      addRoles: {},
      editRolesVisible: false,
      editRoles: {},
      editRolesRules: {
        roleName: [
          {required: true, message: '', trigger: 'blur'},
          {min: 3, max: 10, message: '3~10', trigger: 'blur'}
        ],
        roleDesc: [
          {required: true, message: '', trigger: 'blur'},
          {min: 5, max: 20, message: '5~20', trigger: 'blur'}
        ]
      },
      setRightDialogVisible: false,
      treeProps: {
        children: 'children',
        label: 'authName'
      },
      defKeys: [],
      roleId: ''
    }
  },
  created() {
    this.getRolesList()
  },
  methods: {
   async getRolesList() {
      const {data: res} = await this.$http.get('roles')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rolesList = res.data
     this.$message.success('')
   },
    addRolesInfo() {
      this.$refs.addRolesRef.validate(async valid=> {
        if (!valid) return
        const {data: res} = await this.$http.post('roles',this.addRoles)
        if (res.meta.status !== 201) {
          return this.$message.error('!')
        }
        this.$message.success('')
        this.addRolesVisible = false
        this.getRolesList()
      })

    },
    addRolesClosed() {
     this.$refs.addRolesRef.resetFields()
    },
    editRolesClosed() {
      this.$refs.editRolesRef.resetFields()
    },
    setRightDialogClosed() {
     this.defKeys = []
    },
    async showEditVisible(id) {
      const {data:res} = await this.$http.get('roles/'+id)
      if (res.meta.status !== 200) return this.$message.error('!')
      this.$message.success('')
      console.log(res);
      this.editRoles = res.data
      console.log(this.editRoles);
      this.editRolesVisible = true
    },
    editRolesInfo() {
     this.$refs.editRolesRef.validate(async valid=> {
       if (!valid) return
       const {data: res} = await this.$http.put('roles/'+ this.editRoles.roleId,{
         roleName: this.editRoles.roleName,
         roleDesc: this.editRoles.roleDesc
       })
       if (res.meta.status !== 200) {
         return this.$message.error('')
       }
       this.editRolesVisible = false
       this.getRolesList()
     })
    },
    async removeRolesById(id) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err=> err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
      const {data: res} = await this.$http.delete('roles/' + id)
      if (res.meta.status !== 200) return this.$message.error('')
      this.$message.success('')
      this.getRolesList()
    },
    async removeRightsById(role,rightId) {
      const confirmResult = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err=> err)
      if (confirmResult !== 'confirm') {
        return this.$message.info('')
      }
   const {data: res} = await this.$http.delete(`roles/${role.id}/rights/${rightId}` )
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }

      role.children = res.data
    },
    async showSetRightDialogue(role) {
     this.roleId =role.id
      const {data: res} = await this.$http.get('rights/tree')
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.rightsList = res.data
      console.log(this.rightsList);
      this.getLeafKeys(role,this.defKeys)
      this.setRightDialogVisible = true
    },
    getLeafKeys(node,arr) {
     if(!node.children) {
       return arr.push(node.id)
     }
     node.children.forEach(item=> {
       this.getLeafKeys(item,arr)
     })


    },
    async allotRights() {
     const keys = [
       ...this.$refs.treeRef.getCheckedKeys(),
       ...this.$refs.treeRef.getHalfCheckedKeys()
     ]
      console.log(keys);
     const idStr = keys.join(',')
      const {data: res} = await
      this.$http.post(`roles/${this.roleId}/rights`, {
        rids: idStr
      })

      if (res.meta.status !== 200) {
        return this.$message.error('')
     }
      this.$message.success('')
      this.getRolesList()
      this.setRightDialogVisible = false

    }







  }
}
</script>

<style scoped>
.el-tag {
  margin: 7px;
}
.bdtop {
  border-top: 1px solid #eee;
}
.bdbottom {
  border-bottom: 1px solid #eee;
}
.vcenter {
  display: flex;
  align-items: center;
}
</style>