<template>
<div>
  <!--  -->
  <el-breadcrumb separator-class="el-icon-arrow-right">
    <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
    <el-breadcrumb-item></el-breadcrumb-item>
  </el-breadcrumb>

 <el-card>
  <!--  -->
  <el-row>
    <el-col :span="8">
      <el-input placeholder="" >
        <el-button slot="append" icon="el-icon-search"></el-button>
      </el-input>
    </el-col>
  </el-row>

   <el-table :data="orderList" stripe border >
     <el-table-column type="index"></el-table-column>
     <el-table-column label="" prop="order_number"></el-table-column>
     <el-table-column label="" prop="order_price"></el-table-column>
     <el-table-column label="" prop="pay_status">
       <template v-slot="scope">
         <el-tag type="success" v-if="scope.row.pay_status == '1'"></el-tag>
         <el-tag type="danger" v-else></el-tag>
       </template>
     </el-table-column>
     <el-table-column label="" prop="is_send"></el-table-column>
     <el-table-column label="" prop="create_time">
       <template v-slot="scope">
         {{scope.row.create_time | dateFormat}}
       </template>
     </el-table-column>
     <el-table-column label="">
       <template v-slot="scope">
         <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
         <el-button type="primary" icon="el-icon-edit" size="mini" @click="showBox"></el-button>
         </el-tooltip>

         <el-tooltip  effect="dark" content="" placement="top" :enterable="false">
         <el-button type="success" icon="el-icon-location" size="mini" @click="showProgressBox"></el-button>
         </el-tooltip>
       </template>
     </el-table-column>

   </el-table>

   <el-pagination
       @size-change="handleSizeChange"
       @current-change="handleCurrentChange"
       :current-page="queryInfo.pagenum"
       :page-sizes="[10, 20, 30, 100]"
       :page-size="queryInfo.pagesize"
       layout="total, sizes, prev, pager, next, jumper"
       :total="total">
   </el-pagination>
</el-card>

  <el-dialog
      title="" :visible.sync="AddressVisible" width="50%" @close="addAddressDialogClosed">
    <el-form :model="addressForm" :rules="addressFormRule" ref="addressFormRef" label-width="100px">
      <el-form-item  label="/" prop="address1">
   <el-cascader :options="cityData" v-model="addressForm.address1"></el-cascader>
      </el-form-item>
      <el-form-item  label="" prop="address1">
        <el-input v-model="addressForm.address2"></el-input>
      </el-form-item>
    </el-form>

    <span slot="footer" class="dialog-footer">
    <el-button @click="AddressVisible = false"> </el-button>
    <el-button type="primary" @click="AddressVisible = false"> </el-button>
  </span>
  </el-dialog>

  <el-dialog title="" :visible.sync="progressVisible" width="50%" >
    <el-timeline>
      <el-timeline-item v-for="(item,index) in progressInfo"
          :key="index" :timestamp="item.time" color="green">
        {{item.context}}
      </el-timeline-item>
    </el-timeline>
  </el-dialog>
  </div>
</template>

<script>
import cityData from "@/components/order/citydata";
export default {
  name: "Order",
  data() {
    return {
      queryInfo: {
        query: '',
        pagenum: 1,
        pagesize: 10
      },
      orderList: [],
      total: 0,
      AddressVisible: false,
      addressForm: {
        address1: '',
        address2: ''
      },
      addressFormRule: {
        address1: [
          { required: true, message: '' , trigger: 'blur'}
        ],
        address2: [
          { required: true, message: '' , trigger: 'blur'}
        ]
      },
      cityData,
      progressVisible: false,
      progressInfo: []
    }
  },
  created() {
    this.getOrderList()
  },
  methods: {
    async getOrderList() {
     const {data: res} = await this.$http.get('orders',{
       params: this.queryInfo})
      if (res.meta.status !== 200) return this.$message.error('')
      // console.log(res);
      this.orderList = res.data.goods
      this.total = res.data.total
      console.log(this.orderList);
    },
    handleSizeChange(newSize) {
      this.queryInfo.pagesize = newSize
      this.getOrderList()
    },
    handleCurrentChange(newPage) {
      this.queryInfo.pagenum =
          this.getOrderList()
    },
    showBox() {
      this.AddressVisible = true
    },
    addAddressDialogClosed() {
      this.$refs.addressFormRef.resetFields()
    },
    async showProgressBox() {
     const {data: res} = await this.$http.get('/kuaidi/1106975712662')
      this.progressVisible = true
      if (res.meta.status !== 200) return this.$message.error('')
      // console.log(res.data);
      this.progressInfo = res.data
      console.log(this.progressInfo);
    },

  }
}
</script>

<style scoped>
.el-cascader {
  width: 100%;
}
</style>